import { Box, Button, Grid, TextField, Typography } from '@mui/material';
import React from 'react';
import './style.scss';

const LoginView = (props) => {
    const { postLogin, user, setUser } = props;

    const handleChangeUser = (key, value) => {
        switch (key) {
            case 'username':
                setUser({...user, username: value});
                break;
            case 'password':
                setUser({...user, password: value});
                break;
            default:
                break;
        }
    }

    return (
        <Box className='box-login-page'>
            <Grid container spacing={2}>
                <Grid item xs={6}>
                    <Box className='box-group-icon'>
                        <img src='/icon/AIS_logo_login.svg' alt='' width={191} height={191} style={{marginBottom:'48px'}} />
                        <img src='/icon/Logo_name.svg' alt='' width={253} height={26} />
                        <div>AI for the better life</div>
                    </Box>
                </Grid>
                <Grid item xs={6}>
                    <Box className='box-input'>
                        <p>Đăng nhập</p>
                        <Box className='box-form-input'>
                            <TextField 
                                id="outlined-basic" 
                                label="Tên đăng nhập" 
                                variant="outlined" 
                                className='input-field'
                                required
                                onChange={(e) => handleChangeUser('username', e.target.value)}
                            />
                            <TextField 
                                id="outlined-basic" 
                                label="Mật khẩu"
                                type='password'
                                variant="outlined" 
                                className='input-field'
                                required
                                onChange={(e) => handleChangeUser('password', e.target.value)}
                            />
                            <div>Quên mật khẩu ?</div>
                            <Button onClick={postLogin} className='button-login'>Đăng nhập</Button>
                        </Box>
                    </Box>
                </Grid>
            </Grid>
        </Box>
    );
};

export default LoginView;
